/**************************************************************************
 * IMPORTS
 ***************************************************************************/

// PROJECT: MIXINS
import DirectionMixin from "@/mixins/main/DirectionMixin"
import LayoutMixin from "@/mixins/main/LayoutMixin"
import ReverseMixin from "@/mixins/main/ReverseMixin"
import SizeMixin from "@/mixins/main/SizeMixin"
import ThemeMixin from "@/mixins/main/ThemeMixin"

/**************************************************************************
 * MIXINS > YOUTUBE HIGHLIGHT
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  mixins: [DirectionMixin, LayoutMixin, ReverseMixin, SizeMixin, ThemeMixin],

  props: {
    buttonColor: {
      type: String,
      default: "blue",
      validator(x) {
        return [
          "black",
          "blue",
          "green",
          "grey",
          "orange",
          "purple",
          "red",
          "turquoise",
          "white",
          "yellow"
        ].includes(x)
      }
    },
    buttonContent: {
      type: String,
      required: true
    },
    buttonLeftIcon: {
      type: String,
      default: null
    },
    buttonLink: {
      type: String,
      required: true
    },
    buttonNewTab: {
      type: Boolean,
      default: true
    },
    channelContent: {
      type: String,
      default: null
    },
    channelLink: {
      type: String,
      default: null
    },
    descriptionContent: {
      type: String,
      default: null
    },
    videoAutoplay: {
      type: Boolean,
      default: true
    },
    videoId: {
      type: String,
      required: true
    }
  }
}
